import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';


import { TablesRoutes } from './tables.routing';

import { ExtendedTableComponent } from './extendedtable/extendedtable.component';
import { RegularTableComponent } from './regulartable/regulartable.component';
import { DataTableComponent } from './datatable.net/datatable.component';
import { DataTableComponent2 } from './datatable.net2/datatable2.component';
import { DataTableComponent3 } from './datatable.net3/datatable3.component';


@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(TablesRoutes),
    FormsModule
  ],
  declarations: [
      ExtendedTableComponent,
      DataTableComponent,
      DataTableComponent2,
      DataTableComponent3, 
      RegularTableComponent
  ]
})

export class TablesModule {}



