import { Routes } from '@angular/router';

import { ExtendedTableComponent } from './extendedtable/extendedtable.component';
import { RegularTableComponent } from './regulartable/regulartable.component';
import { DataTableComponent } from './datatable.net/datatable.component';
import { DataTableComponent2 } from './datatable.net2/datatable2.component';
import { DataTableComponent3 } from './datatable.net3/datatable3.component';

export const TablesRoutes: Routes = [
    {
      path: '',
      children: [ {
        path: 'regular',
        component: RegularTableComponent
    }]
    }, {
    path: '',
    children: [ {
      path: 'extended',
      component: ExtendedTableComponent
    }]
    }, {
      path: '',
      children: [ {
        path: 'datatables.net',
        component: DataTableComponent
        },]
    }, {
      path: '',
      children: [ {
        path: 'datatables.net2',
        component: DataTableComponent2
        },]
    },
    {
      path: '',
      children: [ {
        path: 'datatables.net3',
        component: DataTableComponent3
        },]
    }
];



