// IMPORTANT: this is a plugin which requires jQuery for initialisation and data manipulation

import { Component, OnInit } from '@angular/core';
import { MomentoService } from '../../services/momento.service';
import { ConteudoService } from '../../services/conteudo.service';
import { User } from '../../models/user';
import { Momento } from '../../models/momento';
import { Conteudo } from '../../models/conteudo';
import { TipoEvento } from '../../models/tipo_evento';
import { Router } from '@angular/router';
import { LocalStorageService }  from '../../services/local-storage.service';
import { UserService } from '../../services/user.service';



declare interface DataTable {
    headerRow?: string[];
    footerRow?: string[];
    /*    dataRows?: string[][];*/
    dataRows?: any;
}

declare const $: any;

@Component({
    selector: 'app-data-table-cmp3',
    templateUrl: 'datatable3.component.html'
})

export class DataTableComponent3 implements OnInit {
    public dataTable: DataTable = { 
        headerRow: [ 'Título', 'Tipo', 'id', 'Ações'],
        footerRow: [ 'Título', 'Tipo', 'id', 'Ações'],
        dataRows: [
            /*['Airi Satou', 'Andrew Mike', 'Develop', '2013'],
            ['Teste 1', 'Vai', 'Develop', '2014'],
            ['Teste 2', 'Good By to Sadness', 'Develop', '2017']*/
            ]
        };

        public conteudos: Conteudo[] = new Array();
        public array: any[][] = [];
        public table: any = null;
        public momentoAtual: Momento;
        private momentoIdAtual: number;
        public conteudoIdParaDeletar: string;
        public conteudoIndexOnTableParaDeletar: string; //não usando


        constructor (private momentoService: MomentoService, private router: Router, 
            private localStorageService: LocalStorageService,
            private conteudoService: ConteudoService, private userService: UserService){
        }


        ngOnInit() {
            this.momentoIdAtual = +localStorage.getItem("currentIdMomentoEdit");
            this.initTable(this.momentoIdAtual);

        }


        initTable(momentoId: number){

            this.array = [];
            this.momentoService.getMomentoById(momentoId).subscribe(
                userResponse => {

                    this.momentoAtual = JSON.parse(JSON.parse(JSON.stringify(userResponse))._body);
                    
                    this.conteudoService.getConteudosByMomento(this.momentoAtual).subscribe(
                        data => {
                            this.conteudos = JSON.parse(JSON.parse(JSON.stringify(data))._body);
                            console.log(this.conteudos);
                            this.conteudos.forEach( (conteudo) => {
                                this.array.push([conteudo.titulo, 
                                    conteudo.tipoConteudo == 0 ? 'Foto' : conteudo.tipoConteudo == 1 ? 'Vídeo' : '', 
                                    conteudo.conteudoId]);
                            })

                            this.dataTable.dataRows= this.array;

                            var self=this;


                            if (self.table) {
                                self.table.destroy();
                                self.table = null;
                            }

                            setTimeout(function(){
                                self.initDataTableJQ(self.table);
                            }, 10);

                        },

                        error => console.log(error)
                        );

                }, error=> console.log(error)
                );




        }

        initDataTableJQ(table: any) {

            table =$('#datatables').DataTable({
                "pagingType": "full_numbers",
                "lengthMenu": [[10, 25, 50, -1], [10, 25, 50, "All"]],
                responsive: true,
                retrieve: true,
                language: {
                    search: "_INPUT_",
                    searchPlaceholder: "Buscar",
                    "info": "Mostrando página _START_ de _TOTAL_",
                    "emptyTable": "Não há registros disponíveis",
                    "infoEmpty": "Mostrando página 0 de 0",
                    "lengthMenu": "Mostrar _MENU_ registros",
                    paginate: { "previous": "Anterior", 
                    "last": "Último",
                    "next": "Próximo",
                    "first": "Primeiro",

                }
            },
            "columnDefs": [
            {
                "targets": [ 2 ],
                "visible": false,
                "searchable": false
            }]

        });


            var table = $('#datatables').DataTable();

            // Edit record
            table.on( 'click', '.edit', function () {
                var $tr = $(this).closest('tr');

                var data = table.row($tr).data();


            } );

            // Delete a record
       /* table.on( 'click', '.remove', function (e: any) {
            var $tr = $(this).closest('tr');
            table.row($tr).remove().draw();
            e.preventDefault();
        } );*/

        // Like record
      /*  table.on( 'click', '.like', function () {
            alert('You clicked on Like button');
        });*/

        //  Activate the tooltips
        $('[rel="tooltip"]').tooltip();
    }


    newConteudo(){
        localStorage.setItem("currentIdConteudoEdit", '');

        this.router.navigate(['/forms/edicao-conteudo']);
    }

    editConteudo(conteudoId: string){
        this.conteudoService.getConteudoById(+conteudoId).subscribe( 
            conteudo => {
                let conteudoEdit: Conteudo = JSON.parse(JSON.parse(JSON.stringify(conteudo))._body);
                console.log("Conteúdo a editar");
                console.log(conteudoEdit);

                localStorage.setItem("currentIdConteudoEdit", conteudoEdit.conteudoId.toString());
                this.router.navigate(['/forms/edicao-conteudo']);
            },
            error => console.log(error)
            );

        
    }


    deleteConteudo(){
        this.conteudoService.deleteConteudoById(+this.conteudoIdParaDeletar).subscribe(
            success => {
                this.initTable(this.momentoIdAtual);
                this.showNotification('top','right', "Conteúdo apagado com Sucesso!", 2);
                location.reload();
            }, error => console.log(error) );

    }


    navegarTabelaMomento(){

        this.userService.getUserByEmail(localStorage.getItem("currentEmailUserEdit")).subscribe(
            user => {
                let userEditAtual: User = JSON.parse(JSON.parse(JSON.stringify(user))._body);
                localStorage.setItem("currentEmailUserEdit", userEditAtual.email);

                this.router.navigate(['/tables/datatables.net2']);
            },
            error => console.log(error)
            );

    }


    setReferenciaConteudoADeletar(conteudoId: string, i: string){
        this.conteudoIdParaDeletar = conteudoId;
        this.conteudoIndexOnTableParaDeletar = i;
    }


    showNotification(from: any, align: any, message: string, color: number) {

        const type = ['', 'info', 'success', 'warning', 'danger', 'rose', 'primary'];

        /*const color = Math.floor((Math.random() * 6) + 1);*/

        $.notify({
            icon: 'notifications',
            message: message,
        }, {
            type: type[color],
            timer: 7000,
            delay: 10000,
            placement: {
                from: from,
                align: align
            },
            onClose: null
        });
    }



}



