// IMPORTANT: this is a plugin which requires jQuery for initialisation and data manipulation

import { Component, OnInit } from '@angular/core';
import { MomentoService } from '../../services/momento.service';
import { User } from '../../models/user';
import { Momento } from '../../models/momento';
import { TipoEvento } from '../../models/tipo_evento';
import { Router } from '@angular/router';
import { LocalStorageService }  from '../../services/local-storage.service';
import { UserService } from '../../services/user.service';


declare interface DataTable {
    headerRow?: string[];
    footerRow?: string[];
    /*    dataRows?: string[][];*/
    dataRows?: any;
}

declare const $: any;

@Component({
    selector: 'app-data-table-cmp2',
    templateUrl: 'datatable2.component.html',
    styleUrls: ['datatable2.component.css']
})

export class DataTableComponent2 implements OnInit {
    public dataTable: DataTable = {
        headerRow: [ 'Título', 'Data', 'id', 'Ações'],
        footerRow: [ 'Título', 'Data', 'id', 'Ações'],
        dataRows: [
            /*['Airi Satou', 'Andrew Mike', 'Develop', '2013'],
            ['Teste 1', 'Vai', 'Develop', '2014'],
            ['Teste 2', 'Good By to Sadness', 'Develop', '2017']*/
            ]
        };

        public momentos: Momento[] = new Array();
        public array: any[][] = [];
        public table: any = null;
        public userAtual: User;
        public momentoIdParaDeletar: string;
        public momentoIndexOnTableParaDeletar: string; //não usando


        constructor (private momentoService: MomentoService, private router: Router,
            private localStorageService: LocalStorageService, private userService: UserService){
        }


        ngOnInit() {
            this.initTable();



        }


        initTable(){

            this.array = [];
            this.userService.getUserByEmail(localStorage.getItem("currentEmailUserEdit")).subscribe(
                userResponse => {

                    this.userAtual =JSON.parse(JSON.parse(JSON.stringify(userResponse))._body);

                    this.momentoService.getMomentosByUser(this.userAtual).subscribe(
                        data => {
                            this.momentos = JSON.parse(JSON.parse(JSON.stringify(data))._body);
                            console.log(this.momentos);
                            this.momentos.forEach( (momento) => {
                                this.array.push([momento.titulo,
                                    momento.dataInicial, momento.momentoId]);
                            })

                            this.dataTable.dataRows= this.array;

                            var self=this;


                            if (self.table) {
                                self.table.destroy();
                                self.table = null;
                            }

                            setTimeout(function(){
                                self.initDataTableJQ(self.table);
                            }, 10);

                        },

                        error => console.log(error)
                        );

                }, error=> console.log(error)
                );




        }


        navegarTabelaMemorias(){
            this.router.navigate(['/tables/datatables.net']);
        }


        initDataTableJQ(table: any) {


            table =$('#datatables').DataTable({
                "pagingType": "full_numbers",
                "lengthMenu": [[10, 25, 50, -1], [10, 25, 50, "All"]],
                responsive: true,
                retrieve: true,
                language: {
                    search: "_INPUT_",
                    searchPlaceholder: "Buscar",
                    "info": "Mostrando página _START_ de _TOTAL_",
                    "emptyTable": "Não há registros disponíveis",
                    "infoEmpty": "Mostrando página 0 de 0",
                    "lengthMenu": "Mostrar _MENU_ registros",
                    paginate: { "previous": "Anterior",
                    "last": "Último",
                    "next": "Próximo",
                    "first": "Primeiro",

                }
            },
            "columnDefs": [
            {
                "targets": [ 2 ],
                "visible": false,
                "searchable": false
            }]

        });


            var table = $('#datatables').DataTable();

            // Edit record
            table.on( 'click', '.edit', function () {
                var $tr = $(this).closest('tr');

                var data = table.row($tr).data();


            } );

            // Delete a record
       /* table.on( 'click', '.remove', function (e: any) {
            var $tr = $(this).closest('tr');
            table.row($tr).remove().draw();
            e.preventDefault();
        } );*/

        // Like record
      /*  table.on( 'click', '.like', function () {
            alert('You clicked on Like button');
        });*/

        //  Activate the tooltips
        $('[rel="tooltip"]').tooltip();
    }


    newMomento(){

        localStorage.setItem("currentIdMomentoEdit", '');

        this.router.navigate(['/forms/edicao-narrativa']);
    }

    /*editMomento(momentoId: number){
        this.momentoService.getMomentoById(momentoId).subscribe(
            momento => {
                let momentoEdit: Momento = JSON.parse(JSON.parse(JSON.stringify(momento))._body);

                localStorage.setItem("currentIdMomentoEdit", momentoEdit.momentoId.toString());
                this.localStorageService.componente_metodo = { componente:'DataTableComponent2',
                metodo:'editMomento'}
                this.router.navigate(['/forms/wizard2']);
            },
            error => console.log(error)
            );

        }*/

        editMomento(momentoId: number){
            this.momentoService.getMomentoById(momentoId).subscribe(
                momento => {
                    let momentoEdit: Momento = JSON.parse(JSON.parse(JSON.stringify(momento))._body);
                    console.log("Momento a editar:");
                    console.log(momentoEdit);
                    localStorage.setItem("currentIdMomentoEdit", momentoEdit.momentoId.toString());
                    this.router.navigate(['/forms/edicao-narrativa']);
                },
                error => console.log(error)
                );

        }


        editConteudos(momentoId: string){

            this.momentoService.getMomentoById(+momentoId).subscribe(
                momento => {
                    let momentoEditAtual: Momento = JSON.parse(JSON.parse(JSON.stringify(momento))._body);
                    localStorage.setItem("currentEmailUserEdit", this.userAtual.email);

                    localStorage.setItem("currentIdMomentoEdit", momentoEditAtual.momentoId.toString());

                    this.router.navigate(['/tables/datatables.net3']);
                },
                error => console.log(error)
                );

        }


        deleteMomento(){
            this.momentoService.deleteMomentoById(+this.momentoIdParaDeletar).subscribe(
                success => {
                    this.initTable();                    
                    this.showNotification('top','right', "Momento apagado com Sucesso!", 2);
                    location.reload();
                }, error => console.log(error) );

        }


        setReferenciaMomentoADeletar(momentoId: string, i: string){
            this.momentoIdParaDeletar = momentoId;
            this.momentoIndexOnTableParaDeletar = i;
        }


        showNotification(from: any, align: any, message: string, color: number) {

            const type = ['', 'info', 'success', 'warning', 'danger', 'rose', 'primary'];

            /*const color = Math.floor((Math.random() * 6) + 1);*/

            $.notify({
                icon: 'notifications',
                message: message,
            }, {
                type: type[color],
                timer: 7000,
                delay: 10000,
                placement: {
                    from: from,
                    align: align
                },
                onClose: null
            });
        }



    }



