// IMPORTANT: this is a plugin which requires jQuery for initialisation and data manipulation

import { Component, OnInit } from '@angular/core';
import { UserService } from '../../services/user.service';
import { MomentoService } from '../../services/momento.service';
import { User } from '../../models/user';
import { Momento } from '../../models/momento';
import { TipoEvento } from '../../models/tipo_evento';
import { Router } from '@angular/router';
import { LocalStorageService }  from '../../services/local-storage.service';



declare interface DataTable {
    headerRow?: string[];
    footerRow?: string[];
    /*    dataRows?: string[][];*/
    dataRows?: any;
}

declare const $: any;

@Component({
    selector: 'app-data-table-cmp',
    templateUrl: 'datatable.component.html',
    styleUrls: ['datatable.component.css']
})

export class DataTableComponent implements OnInit {

    public dataTable: DataTable = {
        headerRow: [ 'Nome', 'Sobrenome', 'Evento', 'E-mail', 'id', 'Ações'],
        footerRow: [ 'Nome', 'Sobrenome', 'Evento', 'E-mail', 'id', 'Ações'],
        dataRows: [
            /*['Airi Satou', 'Andrew Mike', 'Develop', '2013'],
            ['Teste 1', 'Vai', 'Develop', '2014'],
            ['Teste 2', 'Good By to Sadness', 'Develop', '2017']*/
            ]
        };
        public user: User = new User();
        public users: User[] = new Array();
        public array: string[][];
        public table: any = null;
        public userIdParaDeletar: string;
        public userIndexOnTableParaDeletar: string; //não usando


        constructor (private userService: UserService, private router: Router,
            private localStorageService: LocalStorageService, private momentoService: MomentoService){
        }


        ngOnInit() {

            document.body.style.backgroundImage = "url('../assets/img/login-imersa-22.png')";

            this.initTable();




        }


        initTable(){
            this.array = [];
            this.userService.getAllUsers().subscribe(
                data => {
                    this.users = JSON.parse(JSON.parse(JSON.stringify(data))._body);
                    console.log(this.users);
                    this.users.forEach( (user) => {
                        this.array.push(
                            [
                            user.nome,
                            user.sobrenome,
                            user.tipoEvento == 0 ? 'Casamento' : user.tipoEvento == 1 ? 'Disney' : user.tipoEvento == 2 ? 'Formatura': '',
                            user.email,
                            user.userId.toString()
                            ]);

                    })

                    this.dataTable.dataRows= this.array;

                    var self=this;


                    if (self.table) {
                        self.table.destroy();
                        self.table = null;
                    }

                    setTimeout(function(){
                        self.initDataTableJQ(self.table);
                    }, 10);

                },

                error => console.log(error)
                );


        }


        initDataTableJQ(table: any) {


            table =$('#datatables').DataTable({
                "pagingType": "full_numbers",
                "lengthMenu": [[10, 25, 50, -1], [10, 25, 50, "All"]],
                responsive: true,
                retrieve: true,
                language: {
                    search: "_INPUT_",
                    searchPlaceholder: "Buscar",
                    "info": "Mostrando página _START_ de _TOTAL_",
                    "emptyTable": "Não há registros disponíveis",
                    "infoEmpty": "Mostrando página 0 de 0",
                    "lengthMenu": "Mostrar _MENU_ registros",
                    paginate: { "previous": "Anterior",
                    "last": "Último",
                    "next": "Próximo",
                    "first": "Primeiro",

                }
            },
            "columnDefs": [
            {
                "targets": [ 4 ],
                "visible": false,
                "searchable": false
            }]

        });





            var table = $('#datatables').DataTable();

            // Edit record
            table.on( 'click', '.edit', function () {
                var $tr = $(this).closest('tr');

                var data = table.row($tr).data();

                /*alert( 'You press on Row: ' + data[0] + ' ' + data[1] + ' ' + data[2] + '\'s row.' );*/
            } );

            // Delete a record
          /*  table.on( 'click', '.remove', function (e: any) {
                var $tr = $(this).closest('tr');
                table.row($tr).remove().draw();
                e.preventDefault();
            } );*/

            // Like record
      /*  table.on( 'click', '.like', function () {
            alert('You clicked on Like button');
        });*/

        //  Activate the tooltips
        $('[rel="tooltip"]').tooltip();
    }


    editMomentos(email: string){

        this.userService.getUserByEmail(email).subscribe(
            user => {
                let userEditAtual: User = JSON.parse(JSON.parse(JSON.stringify(user))._body);
                localStorage.setItem("currentEmailUserEdit", userEditAtual.email);

                this.router.navigate(['/tables/datatables.net2']);
            },
            error => console.log(error)
            );

    }

    goToTimeline(email: string){

        this.userService.getUserByEmail(email).subscribe(
            userResponse=> {
                let user = JSON.parse(JSON.parse(JSON.stringify(userResponse))._body);
                
                localStorage.setItem("currentEmailUserEdit", email);
                this.localStorageService.userEditAtual = user;

                this.localStorageService.componente_metodo = { componente:'DataTableComponent',
                metodo:'goToTimeline'};

                this.router.navigate(['/observador/timeline']);
            },
            error => console.log(error)
            );

    }

    deleteUser(){
        this.userService.deleteUserById(+this.userIdParaDeletar).subscribe(
            success => {
                this.initTable();
                this.showNotification('top','right', "Usuário apagado com Sucesso!", 2);
                location.reload();
            }, error => console.log(error) )

    }

    setReferenciaUsuarioADeletar(userId: string, i: string ){
        this.userIdParaDeletar = userId;
        this.userIndexOnTableParaDeletar = i;
    }


    showNotification(from: any, align: any, message: string, color: number) {

        const type = ['', 'info', 'success', 'warning', 'danger', 'rose', 'primary'];

        /*const color = Math.floor((Math.random() * 6) + 1);*/

        $.notify({
            icon: 'notifications',
            message: message,
        }, {
            type: type[color],
            timer: 7000,
            delay: 10000,
            placement: {
                from: from,
                align: align
            },
            onClose: null
        });
    }

    editUsuario(userId: any){


        this.userService.getUserById(+userId).subscribe(
            userResponse => {
                localStorage.setItem("currentIdUserEdit", JSON.parse(JSON.parse(JSON.stringify(userResponse))._body).userId);

                this.router.navigate(['/forms/edicao-usuario']);
            },
            error => console.log(error)
        );

    }



}



